@import '~antd/dist/antd.css';
.gutter-col {
    padding: 16px 16px;
    max-width: 100%;
    flex: 1 0 50%;
}

.gutter-row {
    padding: 0px 16px 16px 16px;
}

[data-theme='compact'] .card-container > .ant-tabs-card > .ant-tabs-content,
.card-container > .ant-tabs-card > .ant-tabs-content {
  margin-top: -16px;
}

[data-theme='compact'] .card-container > .ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane,
.card-container > .ant-tabs-card > .ant-tabs-content > .ant-tabs-tabpane {
  background: #fafafa;
  padding: 16px;
}

[data-theme='compact'] .card-container > .ant-tabs-card > .ant-tabs-bar,
.card-container > .ant-tabs-card > .ant-tabs-bar {
  border-color: #fafafa;
}

[data-theme='compact'] .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab,
.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab {
  border-color: transparent;
  background:transparent;
}

[data-theme='compact'] .card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active,
.card-container > .ant-tabs-card > .ant-tabs-bar .ant-tabs-tab-active {
  border-color: #fafafa;
  background: #fafafa;
}

#preview {
  height:80vh;    
  box-sizing: border-box;
}

#doc-preview {
  height:80vh;
  padding: 10px;
  background-color: #fff;
  overflow-y: scroll;
}

p > img {
  max-width: 100%;
  display: block;
  margin: 3em auto;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
}

blockquote p {
  display: inline;
}

#editor3-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow:scroll;
}

.CodeMirror {
  flex: 1
}

#editorheight {
  height: 80vh;
  display: flex;
  flex-direction: column;
}

.markdown-container {
  margin-bottom: 16px;
}

#headerstyle {
  font-weight:bold;
}

#headerstyle > p {
  padding-bottom: 2px;
}

#panelstyle {
  white-space: pre-wrap;
  font-weight:normal;

}

.p #panelstyle {
  padding-bottom: 2px;
  padding-top: 2px;
}

.description {
  margin-left: 32px;
}

.ant-page-header-heading-title {
  margin-left: 7px;
}

#alertstyle {
  margin: auto;
  position: relative;
  /* text-align: center; */
  top: 50%;
  width: 50%;
  padding-bottom: 2%;
}

#build-exhibit {
  float: right;
}

#create-exhibit {
  float: right;
}

.signin {
  float: right;
}
